import React from 'react';
import styles from './WowItemList.scss';
import Adsense970250 from 'components/adsense/Adsense970250';
import classNames from 'classnames/bind';
// import NumberFormat from 'react-number-format';

const cx = classNames.bind(styles);

const WowItemList = ({wowitems, selStat, isCri, isHas, isMas, isVer, itemSubClass, selSubClass, 
  selStat1, nonStat, setInvenType, selInvenType, setSelOneStat, selOneStat}) => {
  
  let selStatCnt = 0;
  if(isCri == "Y") selStatCnt += 1;
  if(isHas == "Y") selStatCnt += 1;
  if(isMas == "Y") selStatCnt += 1;
  if(isVer == "Y") selStatCnt += 1;

  const subclassAll = ["", "", "", ""];
  const subclassFilter = subclassAll.filter((a) => a != itemSubClass);

  const list = wowitems.map((wowitem, index_num) => {
    // if(index_num >= 11 && index_num <= 30) {
      const {
        zone, voss, voss_seq, info,
        _id
      } = wowitem;
      // console.log(JSON.parse(info));
      let statSameCnt = 0;      
      let statSameNonCnt = 0;      
      let statSameOneStatCnt = 0;      
      const item_id = JSON.parse(info).id;
      const inventory_type = JSON.parse(info).inventory_type;
      const item_subclass = JSON.parse(info).item_subclass;
      const item_name = JSON.parse(info).name;
      
      let stat_value = "";
      let spellDec = "";
      let url = "https://ko.wowhead.com/item="+item_id;      
      
      if(inventory_type.type != 'NON_EQUIP') { // 
        const previewItem = JSON.parse(info).preview_item;

        if(itemSubClass == "" || (item_subclass.name != subclassFilter[0] && item_subclass.name != subclassFilter[1] && item_subclass.name != subclassFilter[2] )) { //     3  
          if(selInvenType == "" || selInvenType == inventory_type.name) {
            if(previewItem.stats != undefined) {
              previewItem.stats.map((stat) => {
                const dispString = stat.display.display_string;

                if(isCri == "Y" && stat.type.type == "CRIT_RATING") {
                  statSameCnt += 1;
                }
                if(isHas == "Y" && stat.type.type == "HASTE_RATING") {
                  statSameCnt += 1;
                }
                if(isMas == "Y" && stat.type.type == "MASTERY_RATING") {
                  statSameCnt += 1;
                }
                if(isVer == "Y" && stat.type.type == "VERSATILITY") {
                  statSameCnt += 1;              
                }

                if(inventory_type.name.indexOf("") > -1) {
                  // console.log(item_name, dispString, selOneStat, dispString.indexOf(selOneStat), statSameOneStatCnt);
                  if(selOneStat == "" || dispString.indexOf(selOneStat) > -1) {
                    statSameOneStatCnt += 1;              
                  }
                }else{
                  statSameOneStatCnt += 1;              
                }

                // console.log(dispString);
                if(inventory_type.name.indexOf("") > -1 || (dispString.indexOf("") == -1 && dispString.indexOf("") == -1 && dispString.indexOf("") == -1)) {
                  if(dispString.indexOf("") == -1) {
                    stat_value += dispString + " ";
                  }
                }              

                if(inventory_type.name == "") {
                  previewItem.spells.map((spell) => {
                    spellDec += spell.description;
                  });
                }
    
                if(nonStat == stat.type.type) {
                  statSameNonCnt += 1;
                }
              });
            }
            
            if(!statSameNonCnt > 0) {
              if(statSameOneStatCnt >= 1) {
                if( (selStatCnt == 1 && statSameCnt >= 1) || (selStatCnt == 2 && statSameCnt >= 2) ) {
                  const sockets = previewItem.sockets != undefined ? "("+previewItem.sockets[0].socket_type.name+")" : "" ;
                  // console.log(previewItem.sockets != undefined ? previewItem.sockets[0] : "");
                  // console.log(previewItem);
                  return (
                    <>
                      <div className={cx("item-info")}>
                        <a href={url} target='_blank'>
                          [{zone+","+voss}]<br/>
                          {item_name}({previewItem.level.value})<br/>
                          <b>{item_subclass.name} / {inventory_type.name} {sockets}</b><br/>
                          {stat_value}
                          {spellDec == "" ? "" : stat_value == "" ? spellDec : <><br/>{spellDec}</>}
                          <br/><br/>
                        </a>
                      </div>
                      <div className={cx("line")}></div>
                    </>
                  );
                }
              }
            }
          }          
        }
      }   
      
    // }
  });
  
  return (
    <div className={cx('wowitem-list')}>
      <div className={cx('wowitem-adsense')}><Adsense970250/></div>
      <div className={cx('wowitem-search')}>
      <span> .<br/>(, ,    .)</span><br/>
        <button className={cx(itemSubClass == '' ? 'btn-sel' : 'btn-nor')} onClick={() => selSubClass('')}></button>
        <button className={cx(itemSubClass == '' ? 'btn-sel' : 'btn-nor')} onClick={() => selSubClass('')}></button>
        <button className={cx(itemSubClass == '' ? 'btn-sel' : 'btn-nor')} onClick={() => selSubClass('')}></button>
        <button className={cx(itemSubClass == '' ? 'btn-sel' : 'btn-nor')} onClick={() => selSubClass('')}></button>
        <br/><br/>
        <span> 2 . ( 1 )</span><br/>
        <button className={cx(isCri == 'Y' ? 'btn-cri' : 'btn-nor')} onClick={() => selStat('')}></button>
        <button className={cx(isHas == 'Y' ? 'btn-has' : 'btn-nor')} onClick={() => selStat('')}></button>
        <button className={cx(isMas == 'Y' ? 'btn-mas' : 'btn-nor')} onClick={() => selStat('')}></button>
        <button className={cx(isVer == 'Y' ? 'btn-ver' : 'btn-nor')} onClick={() => selStat('')}></button>
        <br/><br/>
        <span> 2 .</span><br/>
        <button className={cx(nonStat == 'CRIT_RATING' ? 'btn-cri' : 'btn-nor')} onClick={() => selStat1('CRIT_RATING')}></button>
        <button className={cx(nonStat == 'HASTE_RATING' ? 'btn-has' : 'btn-nor')} onClick={() => selStat1('HASTE_RATING')}></button>
        <button className={cx(nonStat == 'MASTERY_RATING' ? 'btn-mas' : 'btn-nor')} onClick={() => selStat1('MASTERY_RATING')}></button>
        <button className={cx(nonStat == 'VERSATILITY' ? 'btn-ver' : 'btn-nor')} onClick={() => selStat1('VERSATILITY')}></button>
        <br/><br/>
        <span> .</span><br/>
        <button className={cx(selInvenType == ' ' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType(' ')}></button>
        <button className={cx(selInvenType == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('')}></button>
        <button className={cx(selInvenType == ' ' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType(' ')}></button>
        <button className={cx(selInvenType == ' ' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType(' ')}><br/></button>
        <br/>
        <button className={cx(selInvenType == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('')}></button>
        <button className={cx(selInvenType == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('')}></button>
        <button className={cx(selInvenType == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('')}></button>
        <button className={cx(selInvenType == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('')}></button>
        <br/>
        <button className={cx(selInvenType == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('')}></button>
        <button className={cx(selInvenType == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('')}></button>
        <button className={cx(selInvenType == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('')}></button>
        <button className={cx(selInvenType == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('')}></button>
        <br/>
        <button className={cx(selInvenType == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('')}></button>
        <button className={cx(selInvenType == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('')}></button>
        <button className={cx(selInvenType == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('')}></button>
        <button className={cx(selInvenType == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('')}></button>
        <br/><br/>
        <span> 1 .</span><br/>
        <button className={cx(selOneStat == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setSelOneStat('')}></button>
        <button className={cx(selOneStat == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setSelOneStat('')}></button>
        <button className={cx(selOneStat == '' ? 'btn-sel' : 'btn-nor')} onClick={() => setSelOneStat('')}></button>
      </div>
      <div className={cx('wowitem-view')}>
        {wowitems.length === 0 ? 'DB ...' : list}
      </div>
    </div>
  );
};

export default WowItemList;